#settings-container {
  display: flex;
  flex-flow: column nowrap;
  place-items: stretch center;
  place-content: center start;

  overflow: hidden;

  width: calc((var(--default-size-large, 360px) * 1.25) - (var(--default-size-medium, 12px) * 6.00));
  height: 100%;

  background: var(--default-color-background-tertiary, rgba(232, 241, 255));

  > :nth-child(1) {
    display: grid;
    
    flex: auto 1 auto;

    width: auto;
    height: auto;

    box-sizing: border-box;
    padding: calc(var(--default-size-medium, 12px) * 4.00 / 3.00);

    font: bold var(--default-size-medium, 12px) var(--default-font-family, "Outfit", sans-serif);
    color: var(--default-color-text-primary, rgba(51, 51, 51, 1.00));

    /* Title */
    > :nth-child(1) {
      display: flex;
      flex-flow: column nowrap;
      place-items: stretch center;
      place-content: center center;

      width: 100%;
      height: 36px;
      
      box-sizing: border-box;
      padding: var(--default-size-medium, 12px);

      text-transform: uppercase;
    }
  }

  > :nth-child(2){
    display: flex;
    flex-flow: column wrap;
    place-items: stretch center;
    place-content: center center;

    width: 100%;

    box-sizing: border-box;
    padding: calc(var(--default-size-medium, 12px) * 4.00 / 3.00);
    gap: calc(var(--default-size-medium, 12px) * 4.00 / 3.00);

    .admin-button {
      position: relative;
    
      display: flex;
      flex-flow: column wrap;
      place-items: center center;
      place-content: center center;
    
      width: 100%;
      height: calc(var(--default-size-medium, 12px) * 3.00);
    
      box-sizing: border-box;
      border: none;
      border-radius: calc(var(--default-size-small, 1px) * 4.00);
      box-shadow: rgba(0, 0, 0, 0.40) 0px 2px 4px, rgba(0, 0, 0, 0.30) 0px 7px 13px -3px, rgba(0, 0, 0, 0.20) 0px -3px 0px inset;
      padding: 0;
    
      background: var(--default-color-background-primary, rgba(255, 255, 255, 1.00));
    
      font: bold var(--default-size-medium, 12px) var(--default-font-family, "Outfit", sans-serif);
      color: var(--default-color-text-primary, rgba(51, 51, 51, 1.00));
    }
  }
}